body{
  background-color: #101115;
  margin: 0;
}

@font-face {
    font-family: 'Circular Std';
    src: url(/tedora.info/static/media/CIRCULARSTD-MEDIUM.4fcdd97f.OTF);
}

.header_page{
    font-family: 'Circular Std';
    margin: 50px 150px 0 150px;
    height: 90px;
    width: auto;
}
.options{
    float: right;
    justify-content: space-around;
    display: flex;
    list-style: none;
    margin-top: 10px;
    color: white;
    top: 50%;

}
.logo{
    float: left;
}
.opt_header{
   padding-left: 40px;
}
.hire_me{
    float: right;
    margin-left: 20px;
}
.hire_btn{
    display: flex;
    font-family: 'Circular Std';
    color: white;
    background-color: transparent;
    border: 1px white solid;
    border-radius: 5px;
    height: 35px;
    padding: 10px 15px; 
    cursor: pointer;

}
.hire_btn:hover{
    background-color: #3E82E5;
    color: white;
    border: 1px #3E82E5 solid;
}

@font-face {
    font-family: 'Circular Std';
    src: url(/tedora.info/static/media/CIRCULARSTD-MEDIUM.4fcdd97f.OTF);
}

/*          --TITLE---          */
.Title{
    padding: 70px 190px;
    font-family: 'Circular Std';
    height: 400px;
}
.t_btn_contact{
    background-color: #3E82E5;
    color: white;
    width: 230px;
    height: 70px;
    border-radius: 7px;
    margin-top: 30px;
    font-weight: bold;
    font-size: large;
    cursor: pointer;
    border: #3E82E5 1px solid;
    box-shadow: 0;
    transition: box-shadow 0.5s;  
}
.t_btn_contact:hover{
    box-shadow: 2px 2px 70px #3E82E5;
}

/*      ---Carousel---      */

.Carousel{
    display: flex;
}
/*      ---NotFound 404     */
.NotFound{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    position: absolute;
    top: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    left: 50%;
}
